.nav {
    position: fixed;
    top: 0px;
    left: 10px;
    display: flex;
    flex-direction: row;
    background-color: #ff8888;
    padding: 10px 20px;
    
    box-shadow: 1px 1px 15px #ff9999;
    border-radius: 5px;

}

.nav a {
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}

.nav a:not(:last-child) {
    margin-bottom: 5px;
}