.total
{
    text-align: center;
}

.title
{
    font-size: 40px;
    font-family: 'Single Day', cursive;

    margin-bottom: 10px;
    height: 30px;
}

.date
{
    font-size: 25px;
    font-family: 'Gothic A1', sans-serif;
    margin-top: 0px;

}
.space
{
    margin-bottom: 25px;
    height: 30px;

}
.category
{
    font-size: 20px;
    font-family: 'Gothic A1', sans-serif;
    padding-right: 10px;
}

.list 
{
    font-size: 50px;
    font-family: 'Dongle', sans-serif;
    padding: 0;
    margin-bottom: 0;
}

.bible
{
    padding-top: 10px;
    font-size: 50px;
    font-family: 'Dongle', sans-serif;
    height: 30px;
    
    


}

.a{
    color: black;
    text-decoration: tomato;
}

.sel
{
    color: black;
    background-color: tomato;
    border: 1px solid tomato;
    border-radius: 0.5em;
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);

    padding: 0.6em 1.4em 0.5em 0.8em;
    
}

.btn
{
    color: white;
    background-color: tomato;
    padding: 5px;
    border: 0px;
    border-radius: 10px;
    vertical-align: middle;


}

.result
{
    font-size: 17px;
    font-family: 'Gothic A1', sans-serif;
}